:root {
  --bg: #f7f7f8;
  --bubble-assistant: #ffffff;
  --bubble-user: #e8f0fe;
  --text: #111;
  --muted: #6b7280;
  --chip-bg: #f1f5f9;
  --chip-bg-sel: #e2e8f0;
  --border: #e5e7eb;
  --accent: #2563eb;
}

* { box-sizing: border-box; }
body, html, #root { height: 100%; margin: 0; }
body { background: var(--bg); font-family: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto; color: var(--text); }

.chat-wrap {
  display: flex; flex-direction: column;
  height: 100vh; width: 100%;
  align-items: center;
}
.chat-stream {
  flex: 1; width: 100%; max-width: 740px;
  padding: 24px 16px 120px; /* leave space for composer */
  overflow-y: auto;
}
.msg { display: flex; margin: 10px 0; }
.msg.assistant { justify-content: flex-start; }
.msg.user { justify-content: flex-end; }
.bubble {
  max-width: 85%;
  padding: 12px 14px; border-radius: 12px;
  background: var(--bubble-assistant);
  border: 1px solid var(--border);
  box-shadow: 0 1px 1px rgba(0,0,0,0.02);
}
.msg.user .bubble {
  background: var(--bubble-user);
}
.text { line-height: 1.5; white-space: pre-wrap; }
.hint { font-size: 12px; color: var(--muted); margin-top: 6px; }
.chips { display:flex; flex-wrap: wrap; gap: 8px; margin-top: 10px; }
.chip {
  border: 1px solid var(--border); background: var(--chip-bg);
  padding: 6px 10px; border-radius: 999px; cursor: pointer; font-size: 13px;
}
.chip.selected { background: var(--chip-bg-sel); border-color: #d1d5db; }

.skip-row { text-align: right; margin-top: 8px; }
.skip { background: none; border: none; color: var(--muted); cursor: pointer; font-size: 12px; }

.composer {
  position: fixed; bottom: 0; left: 0; right: 0;
  background: linear-gradient(180deg, rgba(247,247,248,0), var(--bg) 30%, var(--bg) 100%);
  padding: 16px 0;
}
.composer > * {
  max-width: 740px; margin: 0 auto; display: flex; gap: 8px; padding: 0 16px;
}
textarea {
  flex: 1; min-height: 44px; max-height: 160px; resize: none;
  border: 1px solid var(--border); border-radius: 10px;
  padding: 12px 12px; outline: none; background: #fff;
}
.send {
  background: var(--accent); color: #fff; border: none; border-radius: 10px;
  padding: 0 16px; min-width: 100px; cursor: pointer; font-weight: 600;
}
.seed-ctrls { display:flex; align-items:center; gap: 12px; }
.seed-count { color: var(--muted); font-size: 13px; }
